.home {
  text-align: center;
  font-family: 'Comfortaa', cursive;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 95%;
}
.home-name {
  color: #1e88e5;
  font-size: 6.3vw;
  border: none;
}
.home-statement {
  color: rgb(0, 111, 238);
  font-size: 6vw;
  display: inline-block;
}

.statement-title {
  background: #ededed;
  background: -webkit-gradient(linear, left top, left bottom, from(#d9eaf3), color-stop(8%, #fff)) 0 4px;
  background: -webkit-linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -o-linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background: linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background-size: 100% 25px;
  border: 1px solid #e2e2e2;
  font-family: 'Shadows Into Light', cursive;
  color: rgba(255, 0, 0, 0.8);
  padding: 5px;
  margin: 10px;
}
.statement-creativity {
  color: #fff;
  text-shadow: 0px 0px 0px #1e88e5, -3px 2px #1e88e5, 0px 4px rgb(240, 240, 120),
    4px 3px 0px salmon, -8px 4px lightgreen;
}
.statement-happy {
  -webkit-animation: change-colors 25s infinite alternate ease-in-out;
          animation: change-colors 25s infinite alternate ease-in-out;
}
.statement-users {
  -webkit-filter: invert(1);
          filter: invert(1);
}

@-webkit-keyframes change-colors {
  0% {
    color: rgb(0, 111, 238);
  }
  25% {
    color: salmon;
  }
  50% {
    color: lightgreen;
  }
  65% {
    color: #ffea28;
  }
  80% {
    color: #ba68c8;
  }
  100% {
    color: rgb(0, 111, 238);
  }
}

@keyframes change-colors {
  0% {
    color: rgb(0, 111, 238);
  }
  25% {
    color: salmon;
  }
  50% {
    color: lightgreen;
  }
  65% {
    color: #ffea28;
  }
  80% {
    color: #ba68c8;
  }
  100% {
    color: rgb(0, 111, 238);
  }
}



